'use strict'
const HOST = 'http://39.100.43.169:8091/api/v1/app';
// 
const requestUrlObj = {};
function deleteRequestUrl(url) {
    delete requestUrlObj[url];
}

//api
export function getApi(name,id,type='detail') {
    const APIS = {
        'news':    `/hot/${id}/`,
        'wx'    :  `/article/wx/${id}/`,
        'video' :  `/video/${id}/`,
        'smallVideo': `/small/video/${id}/`
    }

    if(name){
        return HOST+APIS[name]+type
    }else{
        return {
            host: HOST,
            apis:APIS
        }
    }
}


