import SectionTitle from '@/components/section-title/index.vue';
import RecommendList from '@/components/recommend-list/index.vue';
import {fetchData, getParams,printLog, getDocDate,getBrowserType,setCookie,getCookie} from '@/common/js/common.js'
import {staticData,getApi} from '../source/main.js'

export default {
    name:'DetailPage',
    data(){
        return {
            'recommendData':null,
            'recommendType':'',
            'dataDetail':{},
            'params':{
            },
            'isApp':'',
            'localUrl':''
        }
    },
    props:{

    },
    components:{
        SectionTitle,
        RecommendList
    },

    watch: {

    },
    created: function () {
        this.isApp = getBrowserType('HotNews')
        let params = getParams();
        //this.params = params;
        //printLog(params,'params')
        if(params.id){
            params.type = params.type || 'news';
            this.params = params;
            this.getData(params);
        }else{
            let datas = staticData();
            this.updataPage(datas.detail);
        }

        //
        this.getRecommend(params);

        //hash
        if(!this.isApp){
            let self = this;
            window.addEventListener('hashchange', function (option) {
                //newURL: "http://localhost:8083/app.html#/detail?type=news&id=NEI23DBHD0IC3E4VJ5RB"
                let aid = getParams('id',option.newURL)
                if(aid && aid!=self.params.id){
                    window.location.href = option.newURL
                    self.params.id = aid;
                    self.getData(self.params);
                }
            }, false)
        }

    },

    methods:{
        updataPage:function (data) {
            this.dataDetail = {
                title:data.data.title,
                source:data.data.source,
                timestamp:data.timestamp.substr(0,10),
                content:data.data.content
            };
        },
        //
        getData:function (params) {
            let cookieData = getCookie(params.id+'-'+params.type);
            if(cookieData){
                /*this.dataDetail = JSON.parse(cookieData);
                console.log(this.dataDetail);
                setCookie(params.id+'-'+params.type, '', 0.3);
                return;*/
            }
            fetchData({
                method: 'get',
                url: getApi(params.type, params.id),
                headers:{
                    /*'Access-Control-Allow-Origin':'http://39.100.43.169:8090',
                     'Access-Control-Allow-Credentials':true*/
                },
                success: (data) => {
                    /*console.log('data-----get')
                    console.log(data)*/
                    if(data.code==10000){
                        window.scrollTo(0,0);
                        this.dataDetail=data.data;
                        this.localUrl = location.href.split('?')[0]+'?type='+params.type+'&id='+params.id;
                        //cookie
                        /*console.log(data.data)
                        setCookie('test', JSON.stringify(data.data), 0.3);
                        let cookieData = getCookie('test');
                        console.log('cookieData')
                        console.log(cookieData)*/
                    }
                },
                error:(err)=>{
                    this.dataDetail={
                        error:'!'
                    }
                },
                complete:(data)=>{
                    //console.log(data)
                }
            })
        },
        //
        getRecommend:function (params) {
            fetchData({
                method: 'get',
                url: getApi(params.type, params.id,'recommend'),
                headers:{
                    /*'Access-Control-Allow-Origin':'http://39.100.43.169:8090',
                     'Access-Control-Allow-Credentials':true*/
                },
                success: (data) => {
                    if(data.code==10000){
                        //news - hotNewsList
                        //wx - articleList
                        //video - videoList
                        let type = params.type;
                        this.recommendType = type;
                        if(type=='news'){ //hot news
                            let news = data.data.hotNewsList;
                            this.recommendData = news.map(item=>{
                                item.ptime = getDocDate(item.ptime)
                                return item
                            })
                        }else if(type=='video'){ //video
                            let news = data.data.videoList;
                            this.recommendData = news.map(item=>{
                                item.createTime = getDocDate(item.createTime)
                                return item
                            })
                        }else if(type=='wx'){ //wx
                            let news = data.data.articleList;
                            this.recommendData = news.map(item=>{
                                item.ptime = getDocDate(item.createTime)
                                return item
                            })
                        }else if(type=='smallVideo'){ //wx
                            let news = data.data.articleList;
                            this.recommendData = news.map(item=>{
                                item.ptime = getDocDate(item.createTime)
                                return item
                            })
                        }

                    }
                },
                error:(err)=>{

                },
                complete:(data)=>{
                    //console.log(data)
                }
            })
        },
        handleEmit:function (option) {
            // fromdata.id
            //console.log(option)
            let detailId = option.data.id;
            let toUrl = location.href.split('?')[0]+'?type='+option.data.type+'&id='+detailId
            if(detailId && detailId!=this.params.id){
                if(this.isApp){
                    let schame =  {
                        "name": "openUrl",
                        "params": {
                            "url": toUrl
                        }
                    }
                    window.location.href = 'transfer://'+encodeURI(schame)

                }else{
                    window.location.href = toUrl;
                    this.params.id = detailId;
                    this.getData(this.params);
                }
            }
        },
        //
        setShare(){
            let parma = {
                "name": "share",
                "params": {
                    "imageUrl": "http://cms-bucket.ws.126.net/2019/01/29/7c9d122504ef4d2d9e757779eae2eedc.png",
                    "title": "",
                    "text": "",
                    "url": "https://c.m.163.com/news/a/AEQP2RBA00964LM3.html",
                    "tag": 208
                }
            }
        }
    }

}


