import Vue from 'vue'
import Router from 'vue-router'
import DetailPage from '../detail-page/index.vue';
import DetailVideo from '../detail-video/index.vue';
import SmallVideo from '../small-video/index.vue';
import Default from '../default/index.vue';
import Detail from '../detail/index.vue';

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Default',
      component: Default
    },
      {
          path: '/detail',
          name: 'Detail',
          component: Detail
      },
      {
          path: '/appdetail',
          name: 'DetailPage',
          component: DetailPage
      },
      {
          path: '/video',
          name: 'DetailVideo',
          component: DetailVideo
      },
      {
          path: '/smallVideo',
          name: 'SmallVideo',
          component: SmallVideo
      }
  ]
})



