import SectionTitle from '@/components/section-title/index.vue';
import CommentList from '@/components/comment-list/index.vue';
import {fetchData, getParams,printLog} from '@/common/js/common.js'
import {getApi} from '../source/main.js'

export default {
    name:'DetailPage',
    data(){
        return {
            dataDetail:{},
            propCommentData:''
        }
    },
    props:{

    },
    components:{
        SectionTitle,
        CommentList
    },
    created: function () {
        let params = getParams();
        this.params = params;
        printLog(params,'params')
        if(params.id){
            let type = params.type || 'video';
            fetchData({
                method: 'get',
                url: getApi(type, params.id),
                headers:{
                    /*'Access-Control-Allow-Origin':'http://39.100.43.169:8090',
                     'Access-Control-Allow-Credentials':true*/
                },
                success: (data) => {
                    printLog(data,'data')
                    if(data.code==10000){
                        this.dataDetail=data.data
                    }
                },
                error:(err)=>{
                    this.dataDetail={
                        error:'!'
                    }
                },
                complete:(data)=>{
                    //console.log(data)
                }
            })
        }else{
            /*let datas = staticData();
            this.updataPage(datas.detail);*/
            printLog('')
        }
    },

    methods:{
        updataPage:function (data) {
            this.dataDetail = {
                title:data.data.title,
                source:data.data.source,
                timestamp:data.timestamp.substr(0,10),
                content:data.data.content
            };

        }
    }
}


