import SectionTitle from '@/components/section-title/index.vue';
import CommentList from '@/components/comment-list/index.vue';
import {fetchData, getParams,printLog} from '@/common/js/common.js'
import {staticData,getApi} from '../source/main.js'

export default {
    name:'DetailPage',
    data(){
        return {
            dataDetail:{},
            videoHeight:document.body.clientHeigh,
            propCommentData:[
                {
                    pic:'http://cdn01.happyjuzi.com/content/201605/24/5744398dd841a.jpg!up1',
                    name:'',
                    content:'',
                    zan:'23'
                },
                {
                    pic:'http://cdn01.happyjuzi.com/content/201605/24/5744398dd841a.jpg!up1',
                    name:'',
                    content:'',
                    zan:'23'
                },
                {
                    pic:'http://cdn01.happyjuzi.com/content/201605/24/5744398dd841a.jpg!up1',
                    name:'',
                    content:'',
                    zan:'23'
                }
            ]
        }
    },
    props:{

    },
    created: function () {
        let params = getParams();
        this.params = params;

        this.videoHeight = document.documentElement.clientHeight || document.body.clientHeight;



        if(params.id){
            let type = params.type || 'smallVideo';
            fetchData({
                method: 'get',
                url: getApi(type, params.id),
                headers:{
                    /*'Access-Control-Allow-Origin':'http://39.100.43.169:8090',
                     'Access-Control-Allow-Credentials':true*/
                },
                success: (data) => {
                    console.log(data)
                    if(data.code==10000){
                        this.dataDetail=data.data
                    }
                },
                error:(err)=>{
                    this.dataDetail={
                        error:'!'
                    }
                },
                complete:(data)=>{
                    //console.log(data)
                }
            })
        }else{
            let datas = staticData();
            this.updataPage(datas.detail);
        }
    },
    components:{
        SectionTitle,
        CommentList
    }
}


